:root{
  --before: -30%;
  --right: 130%;
}

.caseMiddle::before{
  content: '';
  position: absolute;
  left: var(--before);
  width: 2px;
  height: 75px;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 115, 72, var(--tw-bg-opacity));
}

.caseMiddle::after{
  content: '';
  position: absolute;
  left: var(--right);
  width: 2px;
  height: 75px;
  --tw-bg-opacity: 1;
  background-color: rgba(255, 115, 72, var(--tw-bg-opacity));
}

@media screen and (max-width: 768px) {
  :root{
    font-size: 10px;
  }

  .caseMiddle::before{
    content: '';
    position: absolute;
    top: -25%;
    left: 50%;
    transform: translateX(-50%);
    width: 75px;
    height: 2px;
  }

  .caseMiddle::before {
    --tw-bg-opacity: 1;
    background-color: rgba(255, 115, 72, var(--tw-bg-opacity));
  }

  .caseMiddle::after{
    content: '';
    position: absolute;
    bottom: -25%;
    left: 50%;
    transform: translateX(-50%);
    width: 75px;
    height: 2px;
  }

  .caseMiddle::after {
    --tw-bg-opacity: 1;
    background-color: rgba(255, 115, 72, var(--tw-bg-opacity));
  }
}

@media screen and (max-width: 425px) {
  :root{
    font-size: 8px;
  }
}

